import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { UICalendarDemoComponent } from "app/showcase/custom-components/ui-calendar-demo/ui-calendar-demo.component";
import { NzCodeBoxModule } from "app/showcase/share/nz-codebox/nz-codebox.module";
import { UICalendarDemoRoutingModule } from "app/showcase/custom-components/ui-calendar-demo/ui-calendar-demo.routing.module";
import { NzGridModule } from "app/showcase/share/nz-grid/nz-grid.module";
import { NzToolTipModule } from "app/showcase/share/nz-tooltip/nz-tooltip.module";
import { UICalendarDemoBasicComponent } from "app/showcase/custom-components/ui-calendar-demo/ui-calendar-demo-basic.component";
import { UICalendarModule } from "app/custom-components/_primeng-ext/calendar/calendar";
import { UICalendarDemoTimeComponent } from "app/showcase/custom-components/ui-calendar-demo/ui-calendar-demo-time.component";
import { UICalendarDemoInlineComponent } from "app/showcase/custom-components/ui-calendar-demo/ui-calendar-demo-inline.component";

@NgModule({
	declarations:[
		UICalendarDemoComponent,
		UICalendarDemoInlineComponent,
		UICalendarDemoTimeComponent,
		UICalendarDemoBasicComponent
	],
	imports: [
		UICalendarDemoRoutingModule,
		CommonModule,
		FormsModule,
		NzGridModule,
		NzToolTipModule,
		NzCodeBoxModule,
		UICalendarModule
	]
})
export class UICalendarDemoModule { }



