import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'ui-calendar-demo-basic',
  template: `
  <div class="ui-g ui-fluid">
  <div class="ui-g-12 ui-md-4">
      <h3>Basic</h3>
      <ui-calendar [(ngModel)]="date1"></ui-calendar> {{date1|date}}
  </div>
  
  <div class="ui-g-12 ui-md-4">
      <h3>本地化</h3>
      <ui-calendar [(ngModel)]="date2" [locale]="zh" dateFormat="dd/mm/yy"></ui-calendar> {{date2|date}}
  </div>
  
  <div class="ui-g-12 ui-md-4">
      <h3>Min-Max</h3>
      <ui-calendar [(ngModel)]="date4" [minDate]="minDate" [maxDate]="maxDate" [readonlyInput]="true"></ui-calendar> {{date4|date}}
  </div>
  
  <div class="ui-g-12 ui-md-4">
      <h3>Disable Days</h3>
      <ui-calendar [(ngModel)]="date5" tabindex="0" [disabledDates]="invalidDates" [disabledDays]="[0,6]" readonlyInput="readonlyInput"></ui-calendar> {{date5|date}}
  </div>
  
  <div class="ui-g-12 ui-md-4">
      <h3>Navigators</h3>
      <ui-calendar [(ngModel)]="date6" [monthNavigator]="true" [yearNavigator]="true" yearRange="2000:2030"></ui-calendar> {{date6|date}}
  </div>
  
  <div class="ui-g-12 ui-md-4">
      <h3>Time Only </h3>
      <ui-calendar [(ngModel)]="date8" [timeOnly]="true"></ui-calendar>
  </div>
  
  <div class="ui-g-12 ui-md-4">
      <h3>Multiple </h3>
      <ui-calendar [(ngModel)]="dates" selectionMode="multiple" readonlyInput="true"></ui-calendar>
  </div>
  
  <div class="ui-g-12 ui-md-4">
      <h3>Range</h3>
      <ui-calendar [(ngModel)]="rangeDates" selectionMode="range" readonlyInput="true"></ui-calendar>
  </div>
  
  <div class="ui-g-12 ui-md-4">
      <h3>Button Bar</h3>
      <ui-calendar [(ngModel)]="date9" showButtonBar="true"></ui-calendar>
  </div>
  
  <div class="ui-g-12 ui-md-4">
      <h3>Date Template</h3>
      <ui-calendar [(ngModel)]="date10">
          <ng-template pTemplate="date" let-date>
              <span [ngStyle]="{backgroundColor: (date.day < 21 && date.day > 10) ? '#7cc67c' : 'inherit'}" style="border-radius:50%">{{date.day}}</span>
          </ng-template>
      </ui-calendar>
  </div>
</div>
  `,
  styles: []
})
export class UICalendarDemoBasicComponent implements OnInit {

  date1: Date;

  date2: Date;

  date3: Date;

  date4: Date;

  date5: Date;

  date6: Date;

  date7: Date;

  date8: Date;

  date9: Date;

  date10: Date;

  date11: Date;

  dates: Date[];

  rangeDates: Date[];

  minDate: Date;

  maxDate: Date;

  es: any;

  tr = {};

  invalidDates: Array<Date>

  zh: any ;

  constructor() {

  }
  ngOnInit() {


    this.zh = {
      firstDayOfWeek: 0,
      dayNames: ["星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"],
      dayNamesShort: ["周日", "周一", "周二", "周三", "周四", "周五", "周六"],
      dayNamesMin: ["日", "一", "二", "三", "四", "五", "六"],
      monthNames: [ "一月","二月","三月","四月","五月","六月","七月","八月","九月","十月","十一月","十二月" ],
      monthNamesShort: [ "一","二","三","四","五","六","七","八","九","十","十一","十二" ]
    }

    this.tr = {
      firstDayOfWeek: 1
    }

    let today = new Date();
    let month = today.getMonth();
    let year = today.getFullYear();
    let prevMonth = (month === 0) ? 11 : month - 1;
    let prevYear = (prevMonth === 11) ? year - 1 : year;
    let nextMonth = (month === 11) ? 0 : month + 1;
    let nextYear = (nextMonth === 0) ? year + 1 : year;
    this.minDate = new Date();
    this.minDate.setMonth(prevMonth);
    this.minDate.setFullYear(prevYear);
    this.maxDate = new Date();
    this.maxDate.setMonth(nextMonth);
    this.maxDate.setFullYear(nextYear);

    let invalidDate = new Date();
    invalidDate.setDate(today.getDate() - 1);
    this.invalidDates = [today, invalidDate];
  }

}



